'use strict';

// MODULES //

import { isPrimitive as isString } from '@stdlib/assert/is-string';
import hasOwnProp from '@stdlib/assert/has-own-property';
import format from '@stdlib/string/format';
import ALIAS_TO_PKG from './../data/data.json';


// MAIN //

/**
* Returns the standalone package name associated with a specified alias.
*
* @param {string} alias - alias
* @throws {TypeError} must provide a string
* @returns {(string|null)} standalone package name
*
* @example
* var v = alias2standalone( 'base.sin' );
* // returns '@stdlib/math-base-special-sin'
*/
function alias2standalone( alias ) {
	if ( !isString( alias ) ) {
		throw new TypeError( format( 'invalid argument. Must provide a string. Value: `%s`.', alias ) );
	}
	if ( hasOwnProp( ALIAS_TO_PKG, alias ) ) {
		return ALIAS_TO_PKG[ alias ];
	}
	return null;
}


// EXPORTS //

export default alias2standalone;
